@import '../../global/variables.scss';
:host{
  font-size: 10px;
}
.dav-chatBubble {

  position: relative;

  &__bubble {
    display: flex;
    margin-bottom: 8px;
  }

  &__user {
    @extend .dav-chatBubble__bubble;
    justify-content: flex-end;

  }

  &__vivi {
    @extend .dav-chatBubble__bubble;
    justify-content: flex-start;
  }

  &__advisor {
    @extend .dav-chatBubble__vivi;
    margin-left: 8px;
  }

  &__advisorAvatar {
    background-color: white;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    padding: 4px;
    margin-top: 8px;
  }

  &__typing {
    display: flex;
    gap: 6px;

    .one,
    .two,
    .three {
      width: 8px;
      height: 8px;
      border-radius: 50px;
      animation: bounce 600ms cubic-bezier(.7, 0, 1, 1) alternate infinite;
      background-color: #aab1bc;
    }

    .two {
      animation-delay: 300ms;
    }

    .three {
      animation-delay: 600ms;
    }
  }

  &__basicBubble {
    display: flex;
    flex-direction: column;
    width: 296px;
    //min-height: 40px;
    padding: 8px;
  }

  &__userBubble {
    @extend .dav-chatBubble__basicBubble;
    background-color: $option-hover;
    border-radius: 8px 8px 0px 8px;
    //padding-bottom: 16px;
    position: relative;
    overflow-wrap: break-word;
  }

  &__viviBubble {
    @extend .dav-chatBubble__basicBubble;
    background-color: white;
    border-radius: 8px 8px 8px 0px;
    //padding-bottom: 16px;
    position: relative;
  }

  &__TypingBubble {
    @extend .dav-chatBubble__viviBubble;
    min-height: 32px;
    width: auto;
    padding: 10px 24px 0 24px;
  }

  &__advisorBubble {
    @extend .dav-chatBubble__viviBubble;
    display: flex;
    position: relative;
    width: 265px;
  }

  &__text {
    @extend %defaultFont;
    font-size: $font_medium;
    color: $text_color;

  }

  &__hour {
    position: absolute;
    bottom: 0;
    right: 0;
    font-size: $font_xsmall;
    color: $gray_6765;
    padding-right: 4px;
    padding-bottom: 4px;
  }
}

@keyframes bounce {
  0% {
    transform: translate(0px, 3px);
  }

  100% {
    transform: translate(0px, 3px);
    box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.2), inset 0 5px 4px 0px rgba(255, 255, 255, 0.2), inset 0 -5px 9px 0px rgba(0, 0, 0, 0.5);
  }
}