import { Component, Host, h, Prop } from '@stencil/core';
import { Sender } from '../chat-component/enums/sender';

/**
 * Represents a chat bubble component.
 */
@Component({
  tag: 'chat-bubble',
  styleUrl: './chat-bubble.scss',
  shadow: true,
})
export class ChatBubble {
  /**
   * The text content of the chat bubble.
   */
  @Prop() text: string = 'Message';

  /**
   * The sender of the chat bubble.
   */
  @Prop() sender: Sender = Sender.VIVI;

  render() {
    return (
      <Host>
        <div class={'dav-chatBubble__' + this.sender}>
          <div class={'dav-chatBubble__' + this.sender + 'Bubble'}>
            <p class="dav-chatBubble__text">{this.text}</p>
          </div>
        </div>
      </Host>
    );
  }
}
